import {apiGet, apiPut, apiDelete, apiPostfile} from "../../assets/js/baseApi";

let getSoluteAppApi = function() {
    return new Promise((resolve, reject) => {
        apiGet("/api/solutionApp").then((data) => {
            resolve(data);
        }).catch((err) => {
            resolve(err.response);
        })
    })
}

let getSoluteAppByNameApi = function(name) {
    return new Promise((resolve, reject) => {
        let data = {
            name
        }
        apiGet("/api/solutionApp/byname", data).then((data) => {
            resolve(data);
        }).catch((err) => {
            resolve(err.response);
        })
    })
} 
let addSoluteAppApi = function( username, solutionAppIcon, {addName, addUrl, addDescription, addPkgname}){
    console.log(username)
    let formdata = new FormData();

    formdata.append("solutionAppIcon", solutionAppIcon);
    formdata.append("name", addName);
    formdata.append("desc", addDescription);
    formdata.append("url", addUrl);
    formdata.append("username", username);
    formdata.append("appname", addPkgname);
    let config = {
        headers: {'Content-Type': 'multipart/form-data'}
    };
    return new Promise((resolve, reject) => {
        apiPostfile('/api/solutionApp/add', formdata, config).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })

}
let editSoluteAppApi = function(oldSoluteAppName, newSoluteAppName, description, url, pkgname){
    return new Promise((resolve, reject) => {
        let data = {
            oldname: oldSoluteAppName,
            name: newSoluteAppName,
            desc: description,
            url: url,
            pkgname
        }
        
        apiPut("/api/solutionApp/byname", data).then((data) => {
            resolve(data);
        }).catch((err) => {
            resolve(err.response);
        })
    })
}

let deleteSoluteAppApi = function(said){
    return new Promise((resolve, reject) => {
        apiDelete("/api/solutionApp/"+said).then((data) => {
            resolve(data);
        }).catch((err) => {
            resolve(err.response);
        })
    })
}

export {
    getSoluteAppApi,
    getSoluteAppByNameApi,
    addSoluteAppApi,
    editSoluteAppApi,
    deleteSoluteAppApi
}


