<template>
    <div>
        <p class="header-line">
            <i class="fa fa-sliders m-r-10" aria-hidden="true"></i>{{$t('solutionApp.solutionAppManager')}}
        </p>
        
        <card-temp 
        :data="solutionAppData" 
        @addCard ="addClick" 
        :bodyDesc= "desc"
        :isCommon="true"
        :isShowAdd = "true"
        headerBg="#eeffee">
            <div class="card-header cf" slot="card-header" slot-scope="props">
                <!-- <span>{{props.item.name}}</span> -->
                <el-dropdown trigger="click" class="fr pointer" size="small" @command="handleCommand">
                    <span class="el-dropdown-link">
                        <i class="fa fa-ellipsis-v" style="" aria-hidden="true"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item :command="{info: props.item, id: 'edit'}">
                            <i class="fa fa-pencil-square-o m-r-5" aria-hidden="true"></i>
                            {{$t('global.rename')}}
                        </el-dropdown-item>
                        <el-dropdown-item :command="{info: props.item, id: 'delete'}">
                            <i class="fa fa-trash-o m-r-7" aria-hidden="true"></i>
                            {{$t('global.delete')}}
                        </el-dropdown-item>
                        <el-dropdown-item :command="{info: props.item, id: 'redirect'}" :disabled="props.item.url === ''">
                            <i class="fa fa-paper-plane-o m-r-5" aria-hidden="true"></i>
                            {{$t('solutionApp.redirect')}}
                        </el-dropdown-item> 
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
            <div slot="card-body-img"  slot-scope="props"
             class="icon pointer" @click="redirect(props.item.url)">
                <img :src="getSrc(props.item.name, props.item.iconAddress)" :onerror="defaultImg" alt="no img" style="width:67px; height: 67px"/>
            </div>
        </card-temp>


        <el-dialog :visible.sync="dialogEditSolutionAppVisible" @close="initEditInfo()">
            <div slot="title" class="dialog-title">
                <i class="fa fa-pencil-square-o m-r-10" aria-hidden="true"></i>
                {{$t('solutionApp.editSolution')}}
            </div>
            <el-form  :model="solutionAppForm" :rules="rules" ref="solutionAppForm" >
                <el-form-item :label="$t('label.solutionName')" :label-width="formLabelWidth" prop="name">
                    <el-input v-model="solutionAppForm.name" autocomplete="off" class="max-w-350"></el-input>
                </el-form-item>
                <el-form-item :label="$t('label.solutionAppPkgName')" :label-width="formLabelWidth" prop="appname">
                    <el-input v-model="solutionAppForm.appname" autocomplete="off" class="max-w-350" :placeholder="$t('solutionApp.inputSolutionAppPkgHolder')"></el-input>
                </el-form-item>
                <el-form-item :label="$t('label.solutionAppUrl')" :label-width="formLabelWidth" prop="url">
                    <el-input v-model="solutionAppForm.url" autocomplete="off" class="max-w-350"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogEditSolutionAppVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="editSolutionApp()">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>
        <el-dialog :visible.sync="dialogAddSolutionAppVisible" @close="initAddInfo()"> 
            <div slot="title" class="dialog-title">
                <i class="fa fa-plus-square-o m-r-10" aria-hidden="true"></i>
                {{$t('solutionApp.addSolution')}}
            </div>
            <el-form :model="addSolutionAppForm" :rules="rules" ref="addSolutionAppForm">
                <el-form-item :label="$t('label.solutionName')" :label-width="formLabelWidth" prop="addName">
                    <el-input v-model="addSolutionAppForm.addName" autocomplete="off" class="max-w-350" :placeholder="$t('solutionApp.inputSolutionAppNameHolder')"></el-input>
                </el-form-item>
                <el-form-item :label="$t('label.solutionAppPkgName')" :label-width="formLabelWidth" prop="addPkgname">
                    <el-input v-model="addSolutionAppForm.addPkgname" autocomplete="off" class="max-w-350" :placeholder="$t('solutionApp.inputSolutionAppPkgHolder')"></el-input>
                </el-form-item>
                <p class="note">
                    <i>{{$t('solutionApp.note')}}</i>
                    {{$t('solutionApp.noteContent')}}
                </p>
                <el-form-item :label="$t('label.solutionAppUrl')" :label-width="formLabelWidth" prop="addUrl">
                    <el-input v-model="addSolutionAppForm.addUrl" autocomplete="off" class="max-w-350" :placeholder="$t('solutionApp.inputSolutionAppUrlHolder')"></el-input>
                </el-form-item>
                 <el-form-item :label="$t('label.uploadSolutionAppIcon')" :label-width="formLabelWidth" prop="solutionAppIcon">
                    <div class="uploadBtn ">
                        <el-button size="mini" type="primary" @click="dispatchUploadFile()" class="m-r-10"><i class="fa fa-cloud-upload m-r-10"></i>{{$t("solutionApp.upload")}}</el-button> 
                        <input type="file" ref="filElem" name="solutionAppIcon" @change="getIconFile($event)">
                        <div class="fileList" v-show="fileListIsShow">
                            <i class="fa fa-file-text-o m-r-10"></i> {{filename}}
                        </div>     
                    </div>
                </el-form-item>
                
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button  @click="dialogAddSolutionAppVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button  type="primary" @click="addSolutionApp">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>
    </div>  
</template>
<style lang="scss" scoped>
    @import "../../assets/css/colors";
    $subContentColor: #606266;
    $box-width: 200px;
    $box-height: 220px;
    .card-header{
        span{
            display: inline-block;
            margin-top: 8px;
            color: #222d32;
        }
        i{
            float: right;
            color: $primary-color;
            padding: 2px 10px;
        }
    }
    .uploadBtn{
        display: inline-block;
        text-align: left;
        input[type=file] {
            display: none;
        }
        .fileList{
            display: inline-block;
            margin: 10px 0;
            font-size: 14px;
            color:  $subContentColor;
        }
    }
    .note{
        margin-left: 200px;
        margin-bottom: 5px;
        color: $darkgray-color;
        font-size:14px;
        i{
            color: $danger-color;
            margin-right: 8px;
            
        }
    }
           
</style>

<script>
    import {
    getSoluteAppApi,
    getSoluteAppByNameApi,
    addSoluteAppApi,
    editSoluteAppApi,
    deleteSoluteAppApi
    } from '../restfulapi/solutionAppApi';
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus } from '@/components/restfulapi/deviceStatusApi';
    import handleResponse from '../restfulapi/handleResponse'
    import {checkSession, getSession } from '../../assets/js/storage';
    import {solutionIconSize} from "../../assets/js/constant";
    import {getServerConfigApi} from "../restfulapi/serverConfigApi"
    import cardTemp from "../../common/cardTemp"

    export default{
        name: 'solutionAppList',
        data(){
            let validateName = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            
            let validateDes = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,48}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyDescriptionError')))
                }else{callback()};
            };

            let validateServerAddress = (rule,value,callback)=>{
                let reg=new RegExp(/(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/);
                if(!reg.test(value)&&value.length != 0){
                    return callback(new Error (this.$t('global.verifyServerAddressError')))
                }else{callback()};
            };

            let validatePkg = (rule,value,callback)=>{
                let reg=new RegExp(/^([a-zA-Z]+[.][a-zA-Z]+)[.]*.*/);
                if(!reg.test(value)&&value.length != 0){
                    return callback(new Error (this.$t('global.verifyPkgError')))
                }else{callback()};
            };
            

            return{
                formLabelWidth: "200px",
                dialogEditSolutionAppVisible: false,
                dialogAddSolutionAppVisible: false,
                selectedSolutionAppName: "",
                solutionAppData: [],
                solutionAppForm: {
                    name: "",
                    //appname is app pkgname
                    appname: "",
                    url: "",
                    description: ''
                },
                addSolutionAppForm: {
                    addName: "",
                    addPkgname: "",
                    addUrl: "http://",
                    addDescription: ""
                },
                rules:{
                    name: {required: true, validator: validateName, trigger: 'blur'},
                    // url: {required: true, validator: validateServerAddress, trigger: 'blur'},
                    url: {required: true, trigger: 'blur'},
                    appname: {required: true, validator: validatePkg, trigger: 'blur'},
                    description: {required: true, validator: validateDes, trigger: 'blur'},
                    // addUrl: {validator: validateServerAddress, trigger: 'blur'},
                    addUrl: {required: true, trigger: 'blur'},
                    addName: {required: true, validator: validateName, trigger: 'blur'},
                    addPkgname: {required: true, validator: validatePkg, trigger: 'blur'},
                    addDescription: {required: true, validator: validateDes, trigger: 'blur'},
                },
                solutionAppIcon: null,
                defaultImg: 'this.src="' + 'static/imgs/solution.png' + '"',
            }
        },
        components: {
            cardTemp
        },
        methods:{

            dispatchUploadFile(){
                this.$refs.filElem.dispatchEvent(new MouseEvent('click')) 
            },

            getIconFile(event){
                this.solutionAppIcon = null;
                let cfile = event.target.files[0];
                let fname  = cfile.name.toLowerCase();
                let imgStr = /\.(jpg|jpeg|png|bmp)$/;
                if(!imgStr.test(fname)){
                    this.$swal("", this.$t('solutionApp.solutionAppIconInfo'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                //2M
                if(cfile.size > solutionIconSize){
                    this.$swal("", this.$t('solutionApp.solutionAppIconSizeInfo'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                this.solutionAppIcon = event.target.files[0];
            },

            getAllSolutionApps(){
                _g.openGlobalLoading();
                getSoluteAppApi().then((data) => {
                    handleResponse(data, (res) => { 
                        if(Object.prototype.toString.call(res)  === '[object Array]'){
                            this.solutionAppData = res;
                        }
                        // console.log(res);
                    })
                })
            },
            addClick(){
                this.dialogAddSolutionAppVisible = true;
            },

            getSrc(name, iconAddress){
                if(iconAddress){
                    return "api/solutionApp/getIconByName/"+name;
                }else{
                    return 'static/imgs/solution.png';
                }
            },

            initEditInfo(){
                 Object.assign(this.$data.solutionAppForm, this.$options.data().solutionAppForm)
            },

            initAddInfo(){
                Object.assign(this.$data.addSolutionAppForm, this.$options.data().addSolutionAppForm);
                this.solutionAppIcon = null;
            },

            editSolutionApp(){
                this.$refs.solutionAppForm.validate((valid) => {
                    if (valid) {
                        this.dialogEditSolutionAppVisible = false;
                        editSoluteAppApi(this.selectedSolutionAppName, this.solutionAppForm.name, this.solutionAppForm.description, this.solutionAppForm.url, this.solutionAppForm.appname).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.getAllSolutionApps();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                        
                    }
                })
            },

            editClick(solutionAppname){
               
                getSoluteAppByNameApi(solutionAppname).then((res) => {
                    handleResponse(res, (data) => {
                        if(_g.isObject(data)){
                            this.solutionAppForm = data;
                            
                        }
                    })
                })
                this.selectedSolutionAppName = solutionAppname;
                this.dialogEditSolutionAppVisible = true;
            },

            deleteSolutionApp(name) {
                _g.swalInfoDo(this.$t('global.areYouSure'),this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        deleteSoluteAppApi(name).then((res) => {
                            handleResponse(res, (data) => {
                                if(data.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.getAllSolutionApps();
                                    })
                                }else{
                                    _g.handleError(data);
                                }
                            })
                        })
                    }
                })
            },

            addSolutionApp(){
                if(checkSession("username")){
                    let username = getSession("username")
                    this.$refs.addSolutionAppForm.validate((valid) => {
                        if (valid) {
                            this.dialogAddSolutionAppVisible = false;
                            addSoluteAppApi(username, this.solutionAppIcon, this.addSolutionAppForm).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status === "CHANGED"){
                                        this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.getAllSolutionApps();
                                        })
                                    }else{
                                        _g.handleError(res);
                                    }
                                })
                            })
                        }
                    })
                }else{
                    this.$swal("", this.$t('global.loginExpired'), "info", {button: this.$t('global.confirm')}).then(() => {
                        this.$router.replace("/");
                        return;
                    })
                }
            },
            handleCommand(command){
                let id=command.id;
                switch(id){
                    case "delete":
                    this.deleteSolutionApp(command.info.said);
                    break;
                    case "edit":
                    let solutionAppname = command.info.name;
                    Object.assign(this.$data.solutionAppForm, this.$options.data().solutionAppForm)
                    getSoluteAppByNameApi(solutionAppname).then((res) => {
                        handleResponse(res, (data) => {
                            if(_g.isObject(data)){
                                this.solutionAppForm = data;
                            }
                        })
                    })
                    this.selectedSolutionAppName = solutionAppname;
                    this.dialogEditSolutionAppVisible = true;
                    break;
                    case "redirect":
                    let solutionAddress = command.info.url;
                    let solutionUrl = "";
                    let urlReg = new RegExp(/(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/);
                    let newTab=window.open('about:blank');
                    if(urlReg.test(solutionAddress)){
                        solutionUrl = solutionAddress;
                        newTab.location.href = solutionUrl;
                    }else{
                        getSolutionAppServiceApi().then((url) => {
                            if(solutionAddress.indexOf("/") == 0){
                                solutionUrl = url+solutionAddress;
                            }else{
                                solutionUrl = url+"/"+solutionAddress;
                            }
                            newTab.location.href = solutionUrl;
                        })
                       
                        
                    }
                    break;
                }
            },
            redirect(solutionAddress){
                if(solutionAddress){
                    let urlReg = new RegExp(/(http|ftp|https):\/\/[\w\-_]+(\.[\w\-_]+)+([\w\-\.,@?^=%&:/~\+#]*[\w\-\@?^=%&/~\+#])?/);
                    let solutionUrl = "";
                    if(urlReg.test(solutionAddress)){
                        solutionUrl = solutionAddress;
                        this.$router.push({name: "solutionAppPage", params: {solutionPageUrl: solutionUrl}});
                    }else{
                        getSolutionAppServiceApi().then((url) => {
                            if(solutionAddress.indexOf("/") == 0){
                                solutionUrl = url+solutionAddress;
                            }else{
                                solutionUrl = url+"/"+solutionAddress;
                            }
                            this.$router.push({name: "solutionAppPage", params: {solutionPageUrl: solutionUrl}});
                        })
                        
                    }
                    
                }
            },  
            getConfiguration(){
                if(checkSession("username")){
                    this.username = getSession("username");
                    getServerConfigApi(this.username).then((res) => {
                        handleResponse(res, (data) => {
                            if(!data.bloburl){
                                this.$message.error(this.$t('configuration.errorInfo'));
                            }
                        })
                    })
                }else{
                    this.$swal("", this.$t('global.loginExpired'), "info", {button: this.$t('global.confirm')}).then(() => {
                        this.$router.replace("/");
                        return;
                    })  
                }

            },
        },

        computed: {
            filename(){
                return this.solutionAppIcon? this.solutionAppIcon.name: "";
            },

            fileListIsShow(){
                return this.solutionAppIcon? true: false;
            },

          
            desc(){
                return  "name";
            }
        },

        created(){
            this.getAllSolutionApps();
            this.getConfiguration();
        },    
    }
</script>




